 import React from 'react'
import Pricing from '../../Pricing'
import Section from '../../Section'
import { homeObjOne, homeObjTwo,homeObjThree,homeObjFour } from './Data'
 
 function Home() {
     return (
         <>
           <Section {...homeObjOne}/> 
           <Section {...homeObjThree}/> 
           <Section {...homeObjTwo}/> 
           <Pricing/>
           <Section {...homeObjFour}/> 
         </>
     )
 }
 export default Home 