import React from 'react';
import { Link } from 'react-router-dom';
import { FaFire } from 'react-icons/fa';
import { Button } from './Button';
import { BsXDiamondFill } from 'react-icons/bs';
import { GiCrystalize } from 'react-icons/gi';
import { IconContext } from 'react-icons/lib';

function Pricing() {
    return (
        <div>
            <IconContext.Provider value={{color:'#000', size: '64'}}>
            <div className="pricing__section">
            <div className="pricing__wrapper">
                <h1 className="pricing__heading">Pricing</h1>
                <div className="princing__contain">

                    <Link to="/sing-up" className="princing__container-card">
                        <div className="princing__container-cardInfo">
                            <div className="icon">
                                <FaFire />
                            </div>
                            <h3>Starter</h3>
                            <h4>%8.99</h4>
                            <p>per month</p>
                            <ul className="princing__container-features">
                                <li>100 Transactions</li>
                                <li>2% Cash back</li>
                                <li>$10,000 Limit</li>
                            </ul>
                            <Button buttonSize='btn--wide' buttonColor='primary'>
                                Choose Plan
                            </Button>
                        </div>
                    </Link>

                    <Link to="/sing-up" className="princing__container-card">
                        <div className="princing__container-cardInfo">
                            <div className="icon">
                                <BsXDiamondFill />
                            </div>
                            <h3>Gold</h3>
                            <h4>$29.99</h4>
                            <p>per month</p>
                            <ul className="princing__container-features">
                                <li>100 Transactions</li>
                                <li>3.5% Cash back</li>
                                <li>$100,000 Limit</li>
                            </ul>
                            <Button buttonSize='btn--wide' buttonColor='primary'>
                                Choose Plan
                            </Button>
                        </div>
                    </Link>

                    <Link to="/sing-up" className="princing__container-card">
                        <div className="princing__container-cardInfo">
                            <div className="icon">
                                <GiCrystalize />
                            </div>
                            <h3>Starter</h3>
                            <h4>%8.99</h4>
                            <p>per month</p>
                            <ul className="princing__container-features">
                                <li>100 Transactions</li>
                                <li>2% Cash back</li>
                                <li>$10,000 Limit</li>
                            </ul>
                            <Button buttonSize='btn--wide' buttonColor='primary'>
                                Choose Plan
                            </Button>
                        </div>
                    </Link>

                </div>
                </div>
            </div>
            </IconContext.Provider>
        </div>
    )
}

export default Pricing
